  height: 72px;
  padding: 0;
  padding-left: 3px;
  align-items: center;
  justify-content: center;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  border-radius: 100%;
  border: none;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: 0 0 0 10px transparent;
  cursor: pointer;
  display: none;
}
.video__button::before {
  position: absolute;
  content: "";
