.video__button:hover svg, .video__button:focus svg {
  transform: translateX(1px) scale(1.2);
}

.is--active .video__button {
  display: flex;
}

.video:hover .video__button-icon {
  transform: translateX(1px) scale(1.2);
  transition: transform 0.03s ease-in-out;
}

.video__frame {
  width: 100%;
  height: 100%;
  border: none;
}

[data-tabs=parent] {
